@import url(./__logo/headr__logo.css);

.header {
  margin: auto;
  border-bottom: 1px rgba(84, 84, 84, 0.7) solid;
}

@media screen and (min-width: 882px) {
  .header {
    padding: 45px 0px 45px;
    max-width: 882px;
    max-height: 74px;
  }
}

@media screen and (max-width: 881px) {
  .header {
    padding: 28px 30px 30px;
    max-width: 324px;
    max-height: 56px;
  }
}
